<template>
    <v-menu
    bottom
    left
    max-height="calc(100% - 16px)"
    offset-y
    transition="slide-y-transition">

    <template v-slot:activator="{ attrs, on }">
      <v-btn
        aria-label="用户信息"
        text
        v-bind="attrs"
        v-on="on"
      >
      
      <v-icon >person_add</v-icon>
        <span class="subtitle-1 text-capitalize font-weight-light hidden-sm-and-down">{{username}}</span>

        <v-icon
          class="hidden-sm-and-down"
          right
        >
          mdi-menu-down
        </v-icon>

        
      </v-btn>
    </template>

    <v-list
      dense
      nav
    >
      <base-item
        v-for="action in actions"
        :key="action.text"
        v-bind="action"
        no-markdown
      />
     
    </v-list>
  </v-menu>


</template>

<script>
import { get} from 'vuex-pathify'
  export default {
      components: {
        BaseItem:() => import('./Item'),
    },
      data:()=>({
          text:"oldking",
          actions:[
              {
                text: "个人资料",
                icon: 'mdi-format-paint',
                href: '/#',
                },
                {
                text: '注销',
                icon: 'mdi-package-variant-closed',
                href: '/logout',
                },
          ]
      }),
      computed:{
        username:get('user/username'),
      }
  }
</script>